.Search{
  float: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column; 
  width: 100%;
  height: 100%;   
  min-width: 900px;
  min-height: 550px;
}
.SearchTabbar{
  width: 80%;
  height: 85px;
  display: -ms-flexbox;
