      flex-direction: row;
  -ms-flex-align: end;
      align-items: flex-end;
}
.SearchTopBox{
  float: left;
  width: 100%;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-size: 100% 100%;
  background-color: rgba(255, 192, 205, 0.2);  
}
.SearchInputBox{
  float: left;
  width: 80%;
  height: 30%;
