  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: white;
  border-radius: 10px;
}
.SearchTopInput{
  float: left;
  width: 80%;
  height: 80%;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  font-size: 140%;
  outline: none;
}  
.SearchList{
  float: left;
  width: 100%;
  
  height: 55%;
}  


