.searchBox{
  float: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  height: 35px;
  width: 150px;
  margin-right: 5%;
  margin-top: 25px;
  border: 1px rgba(128, 128, 128, 0.3) solid;
  border-radius: 8px;
}

.SearchBoxbutton{
  float: right;
  height: 80%;
  width: 20%;
  border: 0;
  background: rgba(0, 0, 0, 0);
  font-size: 100%;
  outline: none;
}
.searchBoxInput{
  float: left;
  border: 0;
  font-size: 100%;
  height: 80%;
  width: 70%;
  margin-left: 10px;
  outline: none;
}



